import type { TransformedError } from '@wix/sdk-runtime/transform-error';
import { MinimalHttpError } from '@wix/error-handler-core/types';

function isObject<T>(thing: unknown): thing is Partial<T> {
  return thing != null;
}

export function isSdkError(err: unknown): err is TransformedError {
  return isObject<TransformedError>(err) && err.status != null;
}

export function sdkErrorToMinimalHttpError(
  err: TransformedError,
): MinimalHttpError {
  return {
    response: {
      data: {
        details: err.details,
      },
      status: err.status,
    },
    requestId: err.requestId,
  };
}
